@import './normalize.scss';
@import './reset.scss';
@import './ComponentsStyles/Messages.scss';

$navBackgroundColor: #EFEFEF;
$navBackgroundColorHover: #E3E3E3;

// === Global >
*,
*::after,
*::before {
    box-sizing: border-box;
}
// === Global <

// === Fonts >
@font-face {
    font-family: 'mrglovani';
    src: url(./../fonts/bpg_mrgvlovani_caps_2010.ttf) format('truetype');
}
//  === Fonts <

.mainApp {
    &-outer {

    }

    &-inner {
        display: flex;
        font-family: 'mrglovani';

        &-nav {
            &-wrapper {
                display: flex;
                flex-direction: column;
                list-style: none;
                background-color: $navBackgroundColor;

                

                &-item {
                    display: flex;
                    flex-direction: column;
                    padding: 40px 0;
                    width: 150px;
                    text-align: center;
                    cursor: pointer;
                    &:first-child {
                        background-color: $navBackgroundColorHover;
                    }

                    &:hover {
                        background-color: $navBackgroundColorHover;
                        transition: .3s;
                    }

                    &-ico {
                        color: #1A95BD;
                        font-size: 26px;
                        margin-bottom: 10px;
                    }

                    &-text {
                        color: black;
                        font-size: 12px;
                    }
                }

                &-messages {
                    background-color: $navBackgroundColorHover;
                }
            }
        }

        &-activePage {
            max-width: 1000px;
            width: 100%;
        }
    }

}
